import React, { useState } from "react";

export default function Textarea(props) {

  const handleUpClick =()=>{

    //console.log("UpperCase was clicked "+ text)
    let newText=text.toUpperCase();
    setText(newText)
    props.showAlert("Converted to uppercase","Success");

  }
  const handleLpClick =()=>{

    //console.log("UpperCase was clicked "+ text)
    let newText=text.toLowerCase();
    setText(newText)
    //props.showAlert("Converted to case","Success");
      
  }

  const handleOnChange=(event)=>{
    //console.log("On change");
    setText(event.target.value)
  }

  const clearClick =()=>{

    //console.log("UpperCase was clicked "+ text)
    let newText=("");
    setText(newText)
      
  }
  const[text,setText]= useState(' ');

  return (
    <>
  <div className="container mb-3" style={{color:props.mode==='dark'?'white':'black'}}>
    
<div className="my-2" >
  <h1 className={`my-2 ${props.mode}`}>{props.headings}</h1>
  <textarea className="form-control" value={text} onChange={handleOnChange} style={{backgroundColor:props.mode==='dark'?'grey':'light',color:props.mode==='dark'?'white':'black'}} id="mybox" rows="8"></textarea>
</div>
<button type="button" className="btn btn-primary mx-1" onClick={handleUpClick}>Convert To UpperCase</button>
<button type="button" className="btn btn-primary mx-1" onClick={handleLpClick}>Convert To LowerCase</button>
<button type="button" className="btn btn-primary mx-1" onClick={clearClick}>Clear</button>

  </div>

    <div className="container my-3" style={{color:props.mode==='dark'?'white':'black'}}>
    <h2>Your text Summary</h2>
    <p>{text.split (" ").length} and {text.length} characters </p>
    <p>{0.008 *text.split( " ").length} Minutes read</p>
    <h2>Perview</h2>
    <p>{text}</p>
    </div>
</>
  );
}
